  export default {
    data(){
			return{
				sortNavData:{},
				sortListData:[],
				id:11
			}
		},
		onLoad(){
			uni.request({
				url:"http://localhost:3001/getSortNavData",
				success:(data)=>{
					this.sortNavData = data.data;
					console.log(data);
					},
				fail:()=>{console.log("请求左侧失败")}
			}),
			uni.request({
				url:"http://localhost:3001/getSortListData",
				success:(data)=>{
					this.sortListData = data.data;
					console.log(data);
					},
				fail:()=>{console.log("请求右侧失败")}
			})
		},
    methods:{
			goSearch(){
				uni.navigateTo({
					url:"../Search/index"
				})
			},
			getId(id){
				this.id = id
			}
    }
  }
